  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

/*
 *  MAIN STYLES
 */
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Oswald", sans-serif;
  font-weight: 300;
  text-transform: uppercase; }

body {
  min-height: 100vh;
  background: linear-gradient(to bottom, rgba(255, 250, 245, 0.75) 0, rgba(255, 250, 245, 0.75) 100%), url(../img/paper.jpg);
  display: grid;
  grid-template-columns: 8rem 1fr;
  grid-template-rows: 3rem 2rem 8rem auto 2rem 1fr 2rem auto;
  grid-template-areas: "n n" ". ." "h ." ". ." ". ." "m m" ". ." "f f"; }
  @media screen and (min-width: 40em) {
    body {
      grid-template-columns: 1fr 7rem minmax(11rem, 56rem) 1rem 2rem 4rem minmax(2rem, 1fr);
      grid-template-rows: 2rem 3.5rem 4.5rem minmax(0px, auto) 2rem 1fr 2rem auto 2rem;
      grid-template-areas: ". . . . . n ." "h h . . . n ." "h h . . . . ." ". . . . . . ." ". . . . . . ." "m m m m m m m" ". . . . . . ." ". f f f f f f" ". . . . . . ."; } }
  body.kind-home {
    grid-template-columns: 1fr;
    grid-template-rows: 3rem auto 2rem 1fr 2rem auto;
    grid-template-areas: "n" "h" "." "m" "." "f"; }
    @media screen and (min-width: 40em) {
      body.kind-home {
        grid-template-columns: 1fr 7rem minmax(11rem, 56rem) 1rem 2rem 4rem minmax(2rem, 1fr);
        grid-template-rows: 2rem 3.5rem 4.5rem minmax(0px, auto) 2rem 1fr 2rem auto 2rem;
        grid-template-areas: ". . . . . n ." "h h h h . n ." "h h h h . . ." "h h h h . . ." ". . . . . . ." "m m m m m m m" ". . . . . . ." ". f f f f f f" ". . . . . . ."; } }

.siteheader {
  background: black;
  grid-area: h;
  display: grid;
  grid-template-columns: minmax(1rem, 1fr) 7rem;
  z-index: 2;
  min-width: 8rem; }
  .siteheader > .inner {
    grid-column: 2 / 3; }
  .kind-home .siteheader {
    display: grid;
    grid-template-columns: minmax(1rem, 1fr) 7rem minmax(11rem, 56rem) 1rem;
    min-width: 20rem;
    padding: 1rem 0; }
    .kind-home .siteheader > .inner {
      color: white;
      grid-column: 2 / 4; }
    .kind-home .siteheader h1 {
      margin: 1rem 0;
      line-height: 1.5;
      font-weight: 700;
      font-size: 4em;
      background: url(../img/logo2.min.svg) no-repeat left top;
      background-size: 1.5em;
      padding: calc(1.5em + 0.5rem) 0 0 0; }
      @media screen and (min-width: 48em) {
        .kind-home .siteheader h1 {
          background-position: left center;
          padding: 0 0 0 calc(1.5em + 1rem); } }
    .kind-home .siteheader p {
      max-width: 40em;
      margin: 1rem 0;
      font-size: 0.875em; }
      @media (min-width: 20em) {
        .kind-home .siteheader p {
          font-size: calc(0.75em + 0.625vw); } }
      @media (min-width: 80em) {
        .kind-home .siteheader p {
          font-size: 1.25em; } }
  .siteheader .logo {
    width: 6rem;
    height: 6rem;
    margin: 1rem 0; }

.pageheader {
  z-index: 1;
  background: rgba(0, 0, 0, 0.8);
  grid-area: 2 / 1 / 5 / -1;
  background-position: center;
  background-size: cover;
  display: grid;
  grid-template-rows: 13rem minmax(0px, auto) 3rem;
  grid-template-columns: minmax(0.5rem, 1fr) minmax(19rem, 70rem) minmax(0.5rem, 1fr);
  grid-template-areas: ". . ." ". t ." ". . ."; }
  @media screen and (min-width: 40em) {
    .pageheader {
      grid-area: 1 / 1 / 5 / -1; } }
  .pageheader .inner {
    grid-area: t; }
  .pageheader h1 {
    margin: 0;
    line-height: 1.2;
    font-size: 3em;
    font-weight: 700;
    color: white;
    text-shadow: 0 0 1rem black; }
    @media (min-width: 20em) {
      .pageheader h1 {
        font-size: calc(2em + 5vw); } }
    @media (min-width: 80em) {
      .pageheader h1 {
        font-size: 6em; } }

.sitenav {
  position: sticky;
  top: 0;
  grid-area: n;
  z-index: 3;
  background: white; }

.sitenav__toggle {
  position: relative;
  display: block;
  width: 3rem;
  height: 3rem;
  overflow: hidden;
  transition: 250ms;
  box-shadow: 0 0 0 0 black inset; }
  @media screen and (min-width: 40em) {
    .sitenav__toggle {
      margin: 2rem auto 0; } }
  .sitenav__toggle:hover {
    background: black;
    box-shadow: 0 0 0 0.5rem black inset; }
    .sitenav__toggle:hover span, .sitenav__toggle:hover:before, .sitenav__toggle:hover:after {
      background: white; }
  .sitenav__toggle span, .sitenav__toggle:before, .sitenav__toggle:after {
    position: absolute;
    display: block;
    width: 1.5rem;
    height: 1px;
    left: 50%;
    background: black;
    transform: translateX(-50%);
    transition: 250ms; }
  .sitenav__toggle span {
    top: 50%;
    text-indent: -999px; }
  .sitenav__toggle:before, .sitenav__toggle:after {
    content: ""; }
  .sitenav__toggle:before {
    top: calc(50% - 0.5rem); }
  .sitenav__toggle:after {
    top: calc(50% + 0.5rem); }

.sitemain {
  grid-area: m;
  display: grid;
  grid-template-columns: minmax(0.5rem, 1fr) minmax(18rem, 45rem) auto minmax(0.5rem, 1fr); }
  @media screen and (min-width: 40em) {
    .sitemain {
      grid-template-columns: minmax(1rem, 1fr) minmax(25rem, 45rem) minmax(0rem, 25em) minmax(1rem, 1fr); } }
  .sitemain p {
    margin: 0 0 1rem;
    grid-column: 2 / 3; }
  .sitemain .lead {
    font-size: 1.25em;
    grid-column: 2 / 4;
    max-width: 55rem;
    margin: 0 0 2rem; }

.test {
  background: #ccc;
  height: 100px;
  margin: 0 0 1rem; }

.align-left, .align-middle, .align-narrow, .align-wide {
  grid-column: 1 / -1; }

@media screen and (min-width: 40em) {
  .align-left, .align-middle {
    grid-column: 2 / 4; }
  .align-narrow {
    grid-column: 2 / 3; }
  .align-wide {
    grid-column: 1 / 5; } }

@media screen and (min-width: 72em) {
  .align-left {
    grid-column: 1 / 3; } }

.sitefooter {
  grid-area: f;
  background: white; }

